.mu-lisder-wrapper {
  width: 100%;
  height: 24px;
  position: relative;
  padding-left: 20px;
  padding-right: 30px;
}
.mu-slider {
  width: 85%;
  position: relative;
  height: 40px;
  left: 10px;
}
.mu-slider-track {
  position: absolute;
  height: 2px;
  left: 0;
  right: 0;
  top: 50%;
  margin-top: -1px;
  background-color: #e5e5e5;
}
.mu-slider-fill {
  position: absolute;
  height: 2px;
  width: 100%;
  background-color: #303133;
  left: 0;
  top: 50%;
  margin-top: -1px;
}
.mu-slider-thumb {
  position: absolute;
  top: 50%;
  width: 12px;
  height: 12px;
  background-color: #303133;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: width .3s, height .3s;
  cursor: pointer;
}
.mu-slider.active .mu-slider-thumb {
  width: 18px;
  height: 18px;
}
.mu-increment,
.mu-decrement {
  width: 18px;
  height: 18px;
  position: absolute;
  top: -7px;
  cursor: pointer;
}
.mu-increment {
  right: 0;
}
.mu-decrement {
  left: 0;
}
