









































































































.menu-bar {
  .widget-box {
    height: 60px;
    display: inline-block;
    list-style: none;
    margin: 0;
    padding: 0;

    .btn-item {
      float: left;
      margin-top: 4px;
      height: 52px;
      padding: 4px 12px;
      cursor: pointer;
      position: relative;

      .btn-container {
        font-size: 12px;
        text-align: center;
        .icon {
          display: block;
          margin: 5px auto 2px;
          font-size: 20px;
          color: #fff;
        }
        .btn-text {
          line-height: 16px;
          color: #ccc;
        }
      }
      .btn-item__list {
        position: absolute;
        top: 52px;
        width: 375px;
        left: -125px;
        height: auto;
        padding-top: 6px;

        .list-container {
          z-index: 22;
          border: 1px solid #ccc;
          border-radius: 4px;
          box-shadow: 0 2px 6px rgba(0,0,0,.1);
          padding: 4px;
          background-color: #fff;
          color: #58666e;
          max-height: 380px;
          min-height: 100px;
          overflow-y: auto;
          .tool-list {
            list-style: none;
            padding: 0;
            li {
              float: left;
              margin: 0 5px 4px;
              .tool-item {
                display: inline-block;
                width: 79px;
                white-space: nowrap;
                overflow: hidden;
                cursor: pointer;
                .tool-item__icon {
                  background-size: 100% 100%;
                  background-repeat: no-repeat;
                  display: inline-block;
                  height: 44px;
                  width: 44px;
                  margin: 8px 12px 0;
                  border-radius: 2px;
                }
              }
              &:hover {
                background-color:rgb(240, 240, 240);
                border-radius:2px;
              }
            }
          }
        }
      }
      &:hover {
        background-color:rgb(88, 88, 88);
        border-radius:2px;
      }
    }
  }
}
