
































































































































@import '../../style/_variables.scss';
.panel-tab {
  padding: 0;
}
.panel-wrap {
  height: calc(100%);
  padding: 15px 0;
  position: relative;
  overflow-y: auto;
  .btn-action {
    background-color: none;
    border: none;
    border-radius: 50%;
  }
  .btn-action:hover {
    background-color: #f5f5f5;
  }
}
.panel-row {
  display: flex;
  font-size: 13px;
  line-height: 36px;
}
.panel-row .svg-icon {
  font-size: 16px;
  color: $gray-color;
}
.panel-label {
  display: inline-block;
  width: 80px;
  height: 36px;
  padding-left: 6px;
  margin-left:15px;
  color: #999;
}
.panel-value {
  min-width: 80px;
  display: flex;
  align-items: center;
}
.panel-slider-wrap {
  flex-grow: 1;
  padding: 6px 5px;
  opacity: 0;
  transition: opacity 0.3s;
}
.panel-row:hover .panel-slider-wrap {
  opacity: 1;
}
.panel-cell {
  flex-grow: 1;
}
.panel-wrap input:checked ~ .svg-icon svg {
  stroke: #333;
}
.panel-select {
  width: 100%;
  height: 32px;
  border: 1px solid #ccc;
}
.panel-wrap hr {
  margin: 20px 0;
  border: none;
  border-top: 1px solid #f5f5f5;
}
.panel-wrap select,
.panel-wrap input[type="text"] {
  /*width: 100%;*/
}
.panel-preview {
  width: 150px;
  height: 32px;
  background: no-repeat center/100%;
  cursor: pointer;
}
.panel-item-new {
  /*padding-bottom: 10px;*/
}
.panel-item-title {
  width: 100%;
  height: 40px;
  line-height: 40px;
  padding: 0 12px;
  background-color: #f5f7fa;
  border-bottom: 1px solid #e6eaf1;
  cursor: pointer;
}
.panel-item-new-set{
  padding: 10px 0;
}
.panel-title-arrow{
  float: right;line-height: 40px
}
