




























































































































































.body {
  width: 100%;
  height: calc(100% - 54px);
  overflow: hidden;
  flex-direction: row;
  &.container {
    padding: 0;
  }
}

.columns {
  height: 100%;
}

.toolbar,
.viewport,
.control-panel {
  height: 100%;
}

.toolbar {
  background: #fff;
  user-select: none;
  box-sizing: content-box;
  &.column {
    flex: none;
    width: 120px;
  }
}

.viewport {
  /*position: relative;*/
  overflow: hidden;
}

.control-panel {
  background: #fff;
  user-select: none;
  &.column {
    flex: none;
    width: 400px;
  }
}

.zoom-wrap {
  width: 500px;
  height: 40px;
  position: absolute;
  bottom: 20px;
  left: 50%;
  margin-left: -250px;
  background-color: #90939975;
  opacity: 0;
  transition: opacity 0.3s;
  z-index: 1;
}

.viewport:hover .zoom-wrap {
  opacity: 1;
}

.zoom-value {
  position: absolute;
  top: 10px;
  color: white;
  right: 8px;
}

#svgspriteit {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.col-gapless .viewport-new {
  position: absolute;
  padding-top:60px;
  padding-left:40px;
  left:0;
  flex:1;
  z-index: 1;
}

.navbar1.container.grid-xl {
  max-width: 12200px;
}

ul li {
  list-style: none;
  margin: 0;
  padding:0;
}

.layer {
  &:hover {
    outline: 1px solid #2196f385;
    background-color: #2196f321;
  }
}

.g-active {
  outline: 1px solid #2196f385 !important;
  background-color: #2196f321;
  &:hover {
    outline: 1px solid #2196f3b5 !important;
  }
  &::after {
    content: attr(data-title);
    background: #2196f385;
    color: #fff;
    position: absolute;
    top: 0;
    right: 0;
    padding: 0 5px;
    font-size: 12px;
    border-radius: 0 0 0 4px;
    line-height: 18px;
  }
}

.lay-cont{
  width:100%;
  height:100%;
  position: absolute;
  background-color:transparent;
  z-index:999;
}
